import Logo from "./Logo";
import Search from "./Search";

type Props = {};

const Navbar = (props: Props) => {
  return (
    <nav>
      <div className="flex items-center justify-between gap-4 mx-auto py-4 w-full px-8">
        {/* Logo */}

        <Logo />

        {/* Search */}

        <Search />

        <div>
          <h1>Darkmode</h1>
          <h1>Profile</h1>
          {/* Darkmode */}
          {/* Profile */}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
